@import "./vars.scss";

.about {
  display: flex;
  margin-bottom: 152px;

  &__heading {
    width: 50%;
    @extend %heading2-styles;
  }

  &__text {
    width: 50%;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 27px;
  }

  &__text--heading {
    width: 100%;
    font-size: 24px;
    line-height: 30px;
    color: #0075ff;
    font-weight: bold;
  }
}
